.navbar-inverse {
  background-color: #080808;
  border-color: #080808;
}
.navbar {
  height: 3vw;
  margin-bottom: 4vw;
  min-height: 40px;
  /* opacity: 0; */
  /* filter: (opacity=80); */
}

.navText {
  color: #ffffff;
  padding-top: 0.2vw;
  /* float: right; */
}

.navButton {
  color: #ffffff;
  margin-left: 1vw;
}

a:hover {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

button {
  background-size: 100%;
}

.board {
  text-align: center;
  width: 40vw;
  height: 40vw;
  margin: auto;
  padding: 0%;
  min-height: 365px;
  min-width: 365px;
}

.board-row {
  width: 100%;
  height: 12.5%;
}

.square {
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 100%;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 12.5%;
  background-size: 100%;
}

.highlight {
  border: 5px solid #f8f6f4;
}

.check {
  border: 5px solid #a70d0d;
}

.square-black {
  background-color: #deb887;
}

.square-white {
  background-color: #632a02;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  font-family: Arial, Helvetica, sans-serif;
  font: bolder;
  border-radius: 5px;
  width: 85%;
  max-width: 500px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mtext {
  margin-top: 2%;
  margin-left: 2%;
}
.mbutton {
  /* margin-left: 80%; */
  margin-right: 2%;
  float: right;
  margin-bottom: 2%;
  /* border-radius: 10px; */
}
.display-block {
  display: block;
}

.display-none {
  display: none;
}

.close {
  color: #aaaaaa;
  float: right;
  margin-right: 1vw;
  margin-top: 0.25vw;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

/*  START PAGE  */

.form {
  width: 400px;
  height: 230px;
  background: #eceaea;
  border-radius: 8px;
  box-shadow: 0 0 40px -10px #000;
  margin: calc(50vh - 220px) auto;
  padding: 20px 30px;
  max-width: calc(100vw - 40px);
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  position: relative;
}
.starth2 {
  margin: 10px 0;
  padding-bottom: 10px;
  width: 280px;
  color: #000;
  border-bottom: 3px solid #000;
}
.startInput {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background: none;
  outline: none;
  resize: none;
  border: 0;
  font-family: "Montserrat", sans-serif;
  transition: all 0.3s;
  border-bottom: 2px solid #b2b2b6;
}
.startInput:focus {
  border-bottom: 2px solid #000;
}

.startButton {
  float: right;
  padding: 8px 12px;
  margin: 8px 0 0;
  font-family: "Montserrat", sans-serif;
  border: 2px solid #000;
  background: 0;
  color: #5a5a6e;
  cursor: pointer;
  transition: all 0.3s;
  border-radius: 8px;
}
.startButton:hover {
  background: #000;
  color: #fff;
}

